import React, { useEffect, useState } from "react";
import s2 from "../../s1-main/App.module.css";
import s from "./HW15.module.css";
import axios from "axios";
import SuperPagination from "./common/c9-SuperPagination/SuperPagination";
import { useSearchParams } from "react-router-dom";
import SuperSort from "./common/c10-SuperSort/SuperSort";

/*
 * 1 -  SuperPagination
 * 2 -  SuperSort
 * 3 -  pureChange 
 * 3 -  sendQuery, onChangePagination, onChangeSort  HW15
 * 4 -      
 * 5 -  HW15  HW5/pages/JuniorPlus
 * */

type TechType = {
  id: number;
  tech: string;
  developer: string;
};

type ParamsType = {
  sort: string;
  page: number;
  count: number;
};

const getTechs = (params: ParamsType) => {
  return axios
    .get<{ techs: TechType[]; totalCount: number }>(
      "https://samurai.it-incubator.io/api/3.0/homework/test3",
      { params }
    )
    .catch((e) => {
      alert(e.response?.data?.errorText || e.message);
    });
};

const HW15 = () => {
  const [sort, setSort] = useState("");
  const [page, setPage] = useState(1);
  const [count, setCount] = useState(4);
  const [idLoading, setLoading] = useState(false);
  const [totalCount, setTotalCount] = useState(100);
  const [searchParams, setSearchParams] = useSearchParams();
  const [techs, setTechs] = useState<TechType[]>([]);

  const sendQuery = (params: ParamsType) => {
    setLoading(true);
    getTechs({
      page: params.page,
      count: params.count,
      sort: params.sort,
    })
      .then((res) => {
        const techData = res?.data?.techs ?? [];
        setTechs(techData);
        setTotalCount(res?.data?.totalCount || 100);
      })
      .catch(() => {
        setTechs([]);
        setTotalCount(0);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const onChangePagination = (newPage: number, newCount: number) => {
    const params: ParamsType = {
      page: newPage,
      count: newCount,
      sort: sort,
    };

    setPage(newPage);
    setCount(newCount);

    sendQuery(params);
    setSearchParams({
      page: newPage.toString(),
      count: newCount.toString(),
      ...(sort ? { sort } : {}),
    });
  };

  const onChangeSort = (newSort: string) => {
    const updatedSort = sort === newSort ? "" : newSort;
    setSort(updatedSort);

    const params: ParamsType = {
      page: 1,
      count: count,
      sort: updatedSort,
    };

    sendQuery(params);
    setSearchParams({
      page: "1",
      count: count.toString(),
      ...(updatedSort ? { sort: updatedSort } : {}),
    });
  };

  useEffect(() => {
    const params = Object.fromEntries(searchParams);
    const parsedPage = Number(params.page) || 1;
    const parsedCount = Number(params.count) || 4;
    const parsedSort = params.sort || "";

    setPage(parsedPage);
    setCount(parsedCount);
    setSort(parsedSort);

    sendQuery({
      page: parsedPage,
      count: parsedCount,
      sort: parsedSort,
    });
  }, []);

  const mappedTechs = techs.map((t) => (
    <div key={t.id} className={s.row}>
      <div id={"hw15-tech-" + t.id} className={s.tech}>
        {t.tech}
      </div>

      <div id={"hw15-developer-" + t.id} className={s.developer}>
        {t.developer}
      </div>
    </div>
  ));

  return (
    <div id={"hw15"} style={{ padding: "0 30px" }}>
      <div className={s2.hwTitle}>Homework #15</div>

      <div className={s2.hw}>
        {idLoading && (
          <div id={"hw15-loading"} className={s.loading}>
            {/* Loading... */}
          </div>
        )}

        <SuperPagination
          page={page}
          itemsCountForPage={count}
          totalCount={totalCount}
          onChange={onChangePagination}
        />

        <div className={s.rowHeader}>
          <div className={s.techHeader}>
            tech
            <SuperSort sort={sort} value={"tech"} onChange={onChangeSort} />
          </div>

          <div className={s.developerHeader}>
            developer
            <SuperSort
              sort={sort}
              value={"developer"}
              onChange={onChangeSort}
            />
          </div>
        </div>

        {mappedTechs}
      </div>
    </div>
  );
};

export default HW15;
