import React, { useEffect, useState } from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import { restoreState } from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const get2digitsString = (time: number) => time < 10 ? '0' + time : time;

    useEffect(() => {
        return () => {
            if (timerId) {
                clearInterval(timerId);
            }
        };
    }, [timerId]);

    const start = () => {
        if (!timerId) {
            const intervalID = Number(setInterval(() => {
                setDate(new Date());
            }, 1000));
            setTimerId(intervalID);
        }
    };

    //   //   (   ,   +1)
    //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    const stop = () => {
        if (timerId) {
            clearInterval(timerId);
            setTimerId(undefined)
        }
        //   //    ,    (timerId <- undefined)

    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const hours = get2digitsString(date.getHours());
    const minutes = get2digitsString(date.getMinutes())
    const seconds = get2digitsString(date.getSeconds());
    const stringTime = `${hours}:${minutes}:${seconds}` || <br /> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  

    const year = date.getFullYear();
    const month = get2digitsString(date.getMonth() + 1);
    const day = get2digitsString(date.getDate());

    const stringDate = `${day}.${month}.${year}` || <br /> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const optionsDay: Intl.DateTimeFormatOptions = { weekday: 'long' };
    const optionsMonth: Intl.DateTimeFormatOptions = { month: 'long' }
    const stringDay = new Intl.DateTimeFormat('en-US', optionsDay).format(date) || <br /> //  
    const stringMonth = new Intl.DateTimeFormat('en-US', optionsMonth).format(date) || <br /> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br />
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={!!timerId} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!timerId} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
